body {
  background-image: url(/img/artiom-vallat-ipjRrYEQtSQ-unsplash.jpg);
  background-position: 50% 60%;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  color: #45565d;
  height: 80vh;
  font-family: "Courier New", Courier, monospace;
  margin: 0;
}

.container {
  display: flex;
  flex-direction: column;
}

.personalinfo {
  background-color: #e1eaee;
  text-align: center;
  padding-top: 70px;
  padding-bottom: 10px;
  margin-left: -1vw;
  margin-right: -10vw;
}
.personalinfo--guest {
  height: 100px;
}

.activities {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  text-align: center;
}
.activities__created {
  margin-top: 10px;
  width: 40vw;
  background-color: #e1eaee;
  border-radius: 25px;
}
.activities__registered {
  margin-top: 10px;
  width: 40vw;
  background-color: #e1eaee;
  border-radius: 25px;
}
.activities__buttons {
  display: flex;
  justify-content: center;
}